export default "/* eslint-disable class-methods-use-this */\n\nimport React from 'react';\nimport { Form, Field } from 'react-final-form';\nimport InputField from 'terra-form-input/lib/InputField';\nimport Checkbox from 'terra-form-checkbox';\nimport CheckboxField from 'terra-form-checkbox/lib/CheckboxField';\nimport TerraField from 'terra-form-field';\nimport Radio from 'terra-form-radio';\nimport RadioField from 'terra-form-radio/lib/RadioField';\nimport Button from 'terra-button';\nimport Spacer from 'terra-spacer';\nimport Select from 'terra-form-select';\n\nconst required = value => (value && value.length > 0 ? undefined : 'Required');\n\nexport default class MainEntry extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {};\n    this.submitForm = this.submitForm.bind(this);\n  }\n\n  submitForm(values) {\n    this.setState({\n      submittedValues: values,\n    });\n  }\n\n  renderForm({ handleSubmit, errors, submitFailed }) {\n    return (\n      <form\n        noValidate\n        onSubmit={handleSubmit}\n      >\n        <Field\n          name=\"description\"\n          validate={required}\n        >\n          {({ input, meta }) => (\n            <InputField\n              inputId=\"description\"\n              label=\"Description\"\n              error={meta.error}\n              isInvalid={meta.submitFailed}\n              inputAttrs={{\n                placeholder: 'Description',\n                ...input,\n              }}\n              onChange={(e) => { input.onChange(e.target.value); }}\n              value={input.value}\n              required\n            />\n          )}\n        </Field>\n        <div>\n          <RadioField\n            legend=\"Which kind of meal would you like?\"\n            isInvalid={submitFailed && errors.meal !== undefined}\n            error={errors.meal}\n          >\n            <Field\n              name=\"meal\"\n              type=\"radio\"\n              value=\"chicken\"\n              validate={required}\n              component={({ input }) => (\n                <Radio\n                  inputAttrs={{ ...input }}\n                  labelText=\"Chicken\"\n                  id={input.id}\n                  name={input.name}\n                  checked={!!input.checked}\n                  onChange={input.onChange}\n                  value={input.value}\n                />\n              )}\n            />\n            <Field\n              name=\"meal\"\n              type=\"radio\"\n              value=\"vegetarian\"\n              validate={required}\n              component={({ input }) => (\n                <Radio\n                  inputAttrs={{ ...input }}\n                  labelText=\"Vegetarian\"\n                  id={input.id}\n                  name={input.name}\n                  checked={!!input.checked}\n                  onChange={input.onChange}\n                  value={input.value}\n                />\n              )}\n            />\n            <Field\n              name=\"meal\"\n              type=\"radio\"\n              value=\"fish\"\n              validate={required}\n              component={({ input }) => (\n                <Radio\n                  inputAttrs={{ ...input }}\n                  labelText=\"Fish\"\n                  id={input.id}\n                  name={input.name}\n                  checked={!!input.checked}\n                  onChange={input.onChange}\n                  value={input.value}\n                />\n              )}\n            />\n          </RadioField>\n        </div>\n        <Field\n          name=\"travel\"\n          type=\"select\"\n          validate={required}\n          component={({ input, meta }) => (\n            <TerraField\n              htmlFor=\"airliner\"\n              label=\"Which Airliner are you traveling on?\"\n              isInvalid={meta.submitFailed && meta.error !== undefined}\n              error={meta.error}\n            >\n              <Select\n                id=\"airliner\"\n                name={input.name}\n                onChange={(value) => { input.onChange(value); }}\n                defaultValue={input.value}\n                placeholder=\"Select an Airline\"\n              >\n                <Select.Option value=\"southwest\" display=\"Southwest\" key=\"southwest\" />\n                <Select.Option value=\"alaska\" display=\"Alaska\" key=\"alaska\" />\n                <Select.Option value=\"hawaii\" display=\"Hawaii\" key=\"hawaii\" />\n              </Select>\n            </TerraField>\n          )}\n        />\n        <div>\n          <CheckboxField\n            legend=\"What are all the conference tracks you plan on attending?\"\n            error={errors.tracks}\n            isInvalid={submitFailed && errors.tracks !== undefined}\n          >\n            <Field\n              name=\"tracks[]\"\n              type=\"checkbox\"\n              value=\"developer\"\n              validate={required}\n              component={({ input }) => (\n                <Checkbox\n                  inputAttrs={{ ...input }}\n                  labelText=\"Developer\"\n                  id={input.id}\n                  name={input.name}\n                  checked={!!input.checked}\n                  onChange={input.onChange}\n                  value={input.value}\n                />\n              )}\n            />\n            <Field\n              name=\"tracks[]\"\n              type=\"checkbox\"\n              value=\"designer\"\n              validate={required}\n              component={({ input }) => (\n                <Checkbox\n                  inputAttrs={{ ...input }}\n                  labelText=\"Designer\"\n                  id={input.id}\n                  name={input.name}\n                  checked={!!input.checked}\n                  onChange={input.onChange}\n                  value={input.value}\n                />\n              )}\n            />\n            <Field\n              name=\"tracks[]\"\n              type=\"checkbox\"\n              value=\"soft_skills\"\n              validate={required}\n              component={({ input }) => (\n                <Checkbox\n                  inputAttrs={{ ...input }}\n                  labelText=\"Soft skills\"\n                  id={input.id}\n                  name={input.name}\n                  checked={!!input.checked}\n                  onChange={input.onChange}\n                  value={input.value}\n                />\n              )}\n            />\n          </CheckboxField>\n        </div>\n        <Button text=\"Submit\" type={Button.Opts.Types.SUBMIT} />\n      </form>\n    );\n  }\n\n  render() {\n    return (\n      <Spacer marginBottom=\"small\">\n        <Form\n          onSubmit={this.submitForm}\n          render={this.renderForm}\n          initialValues={{ description: '' }}\n        />\n        {this.state.submittedValues\n          && (\n          <div>\n            <p>Form Submitted Successfully With</p>\n            <pre>{JSON.stringify(this.state.submittedValues, 0, 2)}</pre>\n          </div>\n          )}\n      </Spacer>\n    );\n  }\n}\n"